(function() {
  'use strict';

  angular
    .module('taylor')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('taylor')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('taylor')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {

    // @ngInject
    function scoreController($scope) {

        var vm = this;
        vm.score = 0;
        vm.currentQuestion = 0;

        // when score is updated in main controller, listen for it here
        $scope.$on('updateScore', function (event, data) {
            vm.score += data.points;
            vm.currentQuestion ++;
        });
    }

    // @ngInject
    function score() {
        return {
            restrict: 'E',
            scope: {},
            templateUrl: 'app/components/directives/score.html',
            link: function () {},
            controller: 'ScoreController as scoreCtrl'
        }
    }
    angular.module('taylor')
        .controller('ScoreController', scoreController)
        .directive('score', score);
})();

(function() {

    // @ngInject
    function quoteController($scope) {

        var vm = this;
        vm.quote = $scope.quote;
        vm.title = $scope.title;

        // when quote is updated in main controller, listen for it here
        $scope.$on('nextQuote', function (event, data) {
            vm.quote = data.quote;
        });
    }

    // @ngInject
    function quote() {
        return {
            restrict: 'E',
            scope: {
                quote: '=',
                title: '='
            },
            templateUrl: 'app/components/directives/quote.html',
            link: function () {
            },
            controller: 'QuoteController as quoteCtrl'
        }
    }
    angular.module('taylor')
        .controller('QuoteController', quoteController)
        .directive('displayQuote', quote);
})();
(function() {
  'use strict';

  angular
    .module('taylor')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($scope, quotes, AppConfig, $timeout) {
    var vm = this;
    vm.appConfig = AppConfig;
    vm.gameLength = AppConfig.GAME_LENGTH;
    vm.quotePosition;
    vm.showButtons = true;
    vm.message = '';
    vm.answering = false;
    vm.numQuestions = 10;

    vm.select = function(index) {

        if (!vm.showButtons) return;

        vm.showButtons = false;
        var points = vm.quote.author == index ? 1 : 0;
        updateScore(points);

        $timeout(function() {
            nextQuote();
            if (vm.quotes.length > 0) {
                vm.showButtons = true;
            }
        }, 2000);
    }
    
    function getQuotes() {
        vm.quotes = quotes.getQuotes();
    }

    function updateScore(points) {
        vm.message = (points == 1) ? 'Nice guess!' : 'Wrong, sorry.';
        $scope.$broadcast('updateScore', { 'points' : points });
    }

    function nextQuote() {
        // remove used quote from array.
        vm.quotes.splice(0, 1);
        vm.quote = vm.quotes[0];
        $scope.$broadcast('nextQuote', { 'quote': vm.quote });
        
        if (vm.quotes.length == 0) {
            vm.showButtons = false;
            vm.message = 'Game Over - refresh to play again.';
        }
    }
    // get 10 quotes.
    getQuotes(vm.numQuestions);
    vm.quote = vm.quotes[0];
  }
})();

